<script>
import Footer from "@/components/Footer.vue";
import { mapGetters, mapActions } from "vuex";
import axios from "axios";
export default {
  name: "Discover",
  components: {
    Footer
  },
  data() {
    return {
      discovery: [],
      isloading: true
    };
  },
  computed: {
    ...mapGetters(["discoveryTopic"])
  },
  methods: {
    ...mapActions(["getDiscoverPost"]),
    populateDiscovery() {
      this.discovery = this.discoveryTopic.articles.results;
    }
  },
  async created() {
    await this.getDiscoverPost();
    this.$nextTick(() => this.populateDiscovery());
    this.isloading = false;
  }
};
