<script>
import { mapGetters, mapActions } from "vuex";
import axios from "axios";
export default {
  name: "userNewsPage",
  data() {
    return {
      userData: [],
      isLoading: true,
      topics: [],
      javascript: [],
      php: [],
      Python: [],
      Java: [],
      Ruby: [],
      c: [],
      Rust: []
    };
  },
  computed: {
    ...mapGetters(["isLoggedIn", "user", "userTopic"])
  },
  methods: {
    ...mapActions(["logout", "getUserProfile", "getTopicpost"]),
    logoutUser() {
      this.logout();
    },
    setTopicsArray() {
      const userTopic = this.userTopic;
      for (let i = 0; i < userTopic.length; i++) {
        if (userTopic[i].topic === "JavaScript") {
          this.javascript = userTopic[i].result.articles.results;
        }
        if (userTopic[i].topic === "PHP") {
          this.php = userTopic[i].result.articles.results;
        }
        if (userTopic[i].topic === "Python") {
          this.Python = userTopic[i].result.articles.results;
        }
        if (userTopic[i].topic === "Java") {
          this.Java = userTopic[i].result.articles.results;
        }
        if (userTopic[i].topic === "Ruby") {
          this.Ruby = userTopic[i].result.articles.results;
        }
        if (userTopic[i].topic === "C") {
          this.c = userTopic[i].result.articles.results;
        }
        if (userTopic[i].topic === "Rust") {
          this.Rust = userTopic[i].result.articles.results;
        }
      }
    }
  },
  async created() {
    const user = await this.getUserProfile();
    await this.getTopicpost(user);
    console.log(await this.getTopicpost(user));
    this.$nextTick(() => this.setTopicsArray());
    this.isLoading = false;
    console.log("created");
  }
};
</script scoped>

